<template>
  <div class="subPage">
    <div class="pageTitle">出库统计</div>
    <div>
      <el-date-picker v-model="dates" type="daterange" align="right" unlink-panels range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions"  format="yyyy 年 MM 月 dd 日"  value-format="yyyy-MM-dd">
      </el-date-picker>
      <el-button class="btn_search" type="success" round @click="search">搜索</el-button>
    </div>
    <div class="contents">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="xzcode" label="箱号" width="100"></el-table-column>
        <el-table-column prop="dep" label="科室" width="180"></el-table-column>
        <el-table-column prop="rksj" label="入库时间" width="180"></el-table-column>
        <el-table-column prop="rkperson" label="入库人" width="100"></el-table-column>
        <el-table-column prop="cksj" label="出库时间" width="180"></el-table-column>
        <el-table-column prop="ckperson" label="出库人" width="100"></el-table-column>
        <el-table-column prop="ckdjr" label="出库接收人" width="180"></el-table-column>
        <el-table-column prop="ckdjcl" label="出库接收车辆" ></el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import {_Widget} from "../../common";
import {Downloader} from "../../common";
import {commonConfig} from "../../common";

export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      dates: '',
      tableData:[]
    };
  },
  methods: {
    search(){
      var thisObject = this;
      var data = {
        type:'cktj',
        startdate:thisObject.dates[0],
        enddate:thisObject.dates[1],
      };
      var url = commonConfig.baseURL + "of_yf_cx";
      Downloader.loadXMLAsync(url + "?as_json=" + JSON.stringify(data), function(e) {
        var xmldoc = e.response;
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          thisObject.tableData = res.data;
        }else{
          thisObject.$message({
            type: 'error',
            message: '未查询到相关数据!'
          });
        }
      },function () {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      })
    }
  },
  created() {

  }

}
</script>
<style lang="scss" scoped>
@import '~@/assets/scss/subPage';
</style>
